<template>
  <div class="category-edit">
    <h1>{{ id ? '' : '' }}</h1>
    <el-form label-width="120px" :inline="true" @submit.native.prevent="save">
      <el-form-item label="">
        <el-select
          v-model="model.parent"
          clearable
          placeholder=""
        >
          <el-option
            v-for="item in parents"
            :key="item._id"
            :label="item.name"
            :value="item._id"
          ></el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="">
        <el-input v-model="model.name"></el-input>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" native-type="submit"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: 'CategoryEdit',
  data () {
    return {
      model: {},
      parents: [],
    }
  },
  props: {
    // id: String,
    id: {}, // 
  },
  created () {
    this.id && this.fetch()
    this.fetchParents()
  },
  methods: {
    async save () {
      if (this.id) {
        await this.$http.put(`rest/categories/${this.id}`, this.model)
      } else {
        // this.$http.post('rest/categories', this.model).then()
        await this.$http.post('rest/categories', this.model)
      }

      // 
      this.$router.push('/categories/list')
      this.$message({
        type: 'success',
        message: ''
      })
    },
    async fetch () {
      const res = await this.$http.get(`rest/categories/${this.id}`)
      this.model = res.data
    },
    async fetchParents () {
      const res = await this.$http.get('rest/categories')
      this.parents = res.data
      // this.parents.push(...res.data)
    },
  },
  components: {

  },
}
</script>

