<template>
  <div class="item-edit">
    <h1>{{ id ? '' : '' }}</h1>
    <el-form
      label-width="120px"
      @submit.native.prevent="save"
    >
      <el-form-item label="">
        <el-input v-model="model.name"></el-input>
      </el-form-item>

      <el-form-item label="">
        <el-upload
          class="avatar-uploader"
          :action="uploadUrl"
          :headers="getAuthHeaders"
          :show-file-list="false"
          :on-success="afterUpload"
        >
          <img
            v-if="model.icon"
            :src="model.icon"
            class="avatar"
          />
          <i
            v-else
            class="el-icon-plus avatar-uploader-icon"
          ></i>
        </el-upload>
      </el-form-item>

      <el-form-item>
        <el-button
          type="primary"
          native-type="submit"
        ></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import uploadFileMixin from '../../mixins/common/uploadFileMixin'
export default {
  name: 'ItemEdit',
  mixins: [uploadFileMixin],
  data() {
    return {
      model: {},
    }
  },
  props: {
    id: {},
  },
  created() {
    this.id && this.fetch()
  },
  methods: {
    async save() {
      if (this.id) {
        await this.$http.put(`rest/items/${this.id}`, this.model)
      } else {
        await this.$http.post('rest/items', this.model)
      }

      // 
      this.$router.push('/items/list')
      this.$message({
        type: 'success',
        message: ''
      })
    },
    async fetch() {
      const res = await this.$http.get(`rest/items/${this.id}`)
      this.model = res.data
    },
    afterUpload(res) {
      // res 
      // this.model.icon = res.url
      /**
       * ,console.log 
       * 
       * this.$set(target, key, value)  -----> 
       * ()
       */
      this.$set(this.model, 'icon', res.url)
    }
  },
  components: {

  },
}
</script>

<style scoped>
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
</style>
