<template>
  <div class="category-list">
    <h1></h1>
    <el-table :data="items">
      <el-table-column prop="_id" label="ID" width="230"> </el-table-column>
      <el-table-column prop="parent.name" label=""> </el-table-column>
      <el-table-column prop="name" label=""> </el-table-column>
      <el-table-column fixed="right" label="" width="180">
        <!-- <template slot-scope="scope"> -->
        <!-- slot-scope vue3 , v-slot  -->
        <template v-slot="scope">
          <el-button @click="edit(scope.row._id)" type="primary" size="small"
            ></el-button
          >
          <el-button @click="remove(scope.row)" type="primary" size="small"
            ></el-button
          >
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  name: 'CategoryList',
  data () {
    return {
      items: []
    }
  },
  created () {
    this.fetch()
  },
  methods: {
    async fetch () {
      const res = await this.$http.get('/rest/categories')
      this.items = res.data
    },
    edit (id) {
      //  / ,
      this.$router.push(`/categories/edit/${id}`)
    },
    remove (row) {
      this.$confirm(` "${row.name}"`, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async () => {
        await this.$http.delete(`/rest/categories/${row._id}`)
        this.$message({
          type: 'success',
          message: ''
        })
        this.fetch()
      })
    },
  },
  components: {

  },
}
</script>

