<template>
  <div class="ad-edit">
    <h1>{{ id ? '' : '' }}</h1>
    <el-form
      label-width="130px"
      @submit.native.prevent="save"
    >
      <el-form-item label="">
        <el-input v-model="model.name"></el-input>
      </el-form-item>

      <el-form-item label="">
        <el-button
          size="small"
          @click="model.itemImages.push({})"
        >
          <i class="el-icon-plus" /> 
        </el-button>
        <el-row
          type="flex"
          style="flex-wrap: wrap;margin: 1rem 0"
        >
          <el-col
            :md="24"
            v-for="(item, index) in model.itemImages"
            :key="index"
          >
            <el-card class="box-card">
              <el-form-item label="URL">
                <el-input v-model="item.url"></el-input>
              </el-form-item>

              <el-form-item
                label=""
                style="margin-top:0.5rem"
              >
                <el-upload
                  class="avatar-uploader"
                  :action="uploadUrl"
                  :headers="getAuthHeaders"
                  :show-file-list="false"
                  :on-success="afterUpload"
                >
                  <img
                    v-if="item.images"
                    :src="item.images"
                    class="avatar"
                  />
                  <i
                    v-else
                    class="el-icon-plus avatar-uploader-icon"
                  ></i>
                </el-upload>
              </el-form-item>

              <el-form-item>
                <el-button
                  size="small"
                  type="danger"
                  @click="model.itemImages.splice(index, 1)"
                ></el-button>
              </el-form-item>
            </el-card>
          </el-col>
        </el-row>
      </el-form-item>

      <el-form-item>
        <el-button
          type="primary"
          native-type="submit"
        ></el-button>
      </el-form-item>

    </el-form>
  </div>
</template>

<script>
import uploadFileMixin from '../../mixins/common/uploadFileMixin'
export default {
  name: 'AdEdit',
  mixins: [uploadFileMixin],
  data() {
    return {
      model: {
        itemImages: []
      },
    }
  },
  props: {
    // id: String,
    id: {}, // 
  },
  created() {
    this.id && this.fetch()
  },
  methods: {
    async save() {
      if (this.id) {
        await this.$http.put(`rest/ads/${this.id}`, this.model)
      } else {
        // this.$http.post('rest/ads', this.model).then()
        await this.$http.post('rest/ads', this.model)
      }

      // 
      this.$router.push('/ads/list')
      this.$message({
        type: 'success',
        message: ''
      })
    },
    async fetch() {
      const res = await this.$http.get(`rest/ads/${this.id}`)
      // this.model = res.data
      this.model = Object.assign({}, this.model, res.data)
    },
    afterUpload(res) {
      // res 
      // this.item.images = res.url
      /**
       * ,console.log 
       * 
       * this.$set(target, key, value)  -----> 
       * ()
       */
      this.$set(this.item, 'images', res.url)
    }
  },
  components: {

  },
}
</script>

<style scoped>
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  min-width: 5rem;
  height: 5rem;
  line-height: 5rem;
  text-align: center;
}
.avatar {
  min-width: 5rem;
  height: 5rem;
  display: block;
}
</style>
