<template>
  <div class="login-container">
    <el-card
      header=""
      class="login-card"
    >
      <el-form @submit.native.prevent="login">
        <el-form-item label=''>
          <el-input v-model="model.userName"></el-input>
        </el-form-item>

        <el-form-item label=''>
          <el-input
            show-password
            v-model="model.password"
          ></el-input>
        </el-form-item>

        <el-form-item>
          <el-button
            type="primary"
            native-type="submit "
          ></el-button>
        </el-form-item>
      </el-form>
    </el-card>

  </div>
</template>
<script>
export default {
  data() {
    return {
      model: {},
    };
  },
  methods: {
    async login() {
      const res = await this.$http.post('login', this.model)
      // 
      localStorage.token = res.data.token
      // 
      // sessionStorage.token = res.data.token
      this.$router.push('/')
      this.$message({
        type: 'success',
        message: ''
      })
    }
  }
}

</script>
<style scoped>
.login-container {
  width: 25rem;
  margin: 5rem auto;
}
</style>